import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class StaffService {
  constructor(private httpClient: HttpClient) { }
  
  createStaff(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/staff';
    return this.httpClient.post(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateStaff(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/staff';
    return this.httpClient.put(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  getStaff(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/staff';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  deleteStaff(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/staff';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getStaffList(): Observable<any> {
    const url = SERVER_ENDPOINT + '/staff/list';
    return this.httpClient.get(url).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getStaffListForVendor(owner_id?: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/staff/list';
    return this.httpClient.get(url,{ params: { owner_id }}).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

}
