import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class AreaService {


  constructor(private httpClient: HttpClient) { }
  
  addArea(area: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/area';
    return this.httpClient.post(url, area).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateArea(area: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/area';
    return this.httpClient.put(url, area).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  getArea(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/area';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  deleteArea(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/area';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getAreaList(): Observable<any> {
    const url = SERVER_ENDPOINT + '/area/list';
    return this.httpClient.get(url).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
