import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class SubcategoryService {


  constructor(private httpClient: HttpClient) { }
  
  addSubcategory(category: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/subcategory';
    return this.httpClient.post(url, category).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateSubcategory(category: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/subcategory';
    return this.httpClient.put(url, category).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  getSubcategory(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/subcategory';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  deleteSubcategory(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/subcategory';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getSubcategoryList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/subcategory/list';
    return this.httpClient.get(url, { params: { owner_id }}).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  getSubcategoryListForCategory(owner_id: string, category_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/subcategory/list';
    return this.httpClient.get(url, { params: { owner_id, category_id }}).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
