import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class TableService {


  constructor(private httpClient: HttpClient) { }

  createTable(Table: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/table';
    return this.httpClient.post(url, Table).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateTable(Table: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/table';
    return this.httpClient.put(url, Table).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getTable(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/table';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  deleteTable(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/table';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getTableList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/table/list';
    return this.httpClient.get(url,
      {
        params: { owner_id }
      }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
