import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class VendorService {

  constructor(private httpClient: HttpClient) { }

  createVendor(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/vendor';
    return this.httpClient.post(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateVendor(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/vendor';
    return this.httpClient.put(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getVendor(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/vendor';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  deleteVendor(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/vendor';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getVendorList(): Observable<any> {
    const url = SERVER_ENDPOINT + '/vendor/list';
    return this.httpClient.get(url).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }


  getVendorListForCuisine(cuisine: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/vendor/list';
    return this.httpClient.get(url, {
      params: { cuisine }
    }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

}
