import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class CategoryService {


  constructor(private httpClient: HttpClient) { }
  
  addCategory(category: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/category';
    return this.httpClient.post(url, category).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateCategory(category: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/category';
    return this.httpClient.put(url, category).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  getCategory(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/category';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  deleteCategory(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/category';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getCategoryList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/category/list';
    return this.httpClient.get(url, { params: { owner_id }}).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
