import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class DashboardApiService {


  constructor(private httpClient: HttpClient) { }

  getStatsForAdmin(): Observable<any> {
    const url = SERVER_ENDPOINT + '/dashboard/admin';
    return this.httpClient.get(url).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getStatsForVendor(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/dashboard/vendor';
    return this.httpClient.get(url, {
      params: {
        vendor_id: id
      }
    }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
