import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class ManagerService {

  constructor(private httpClient: HttpClient) { }
  
  createManager(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/manager';
    return this.httpClient.post(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateManager(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/manager';
    return this.httpClient.put(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  getManager(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/manager';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  deleteManager(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/manager';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getManagerList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/manager/list';
    return this.httpClient.get(url,{ params: { owner_id }}).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }}
