import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { tap } from 'rxjs/operators';

@Injectable()
export class ProductService {

  constructor(private httpClient: HttpClient) { }

  createProduct(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/product';
    return this.httpClient.post(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateProduct(user: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/product';
    return this.httpClient.put(url, user).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getProduct(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/product';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  deleteProduct(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/product';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getProductList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/product/list';
    return this.httpClient.get(url,
      {
        params: { owner_id }
      }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }


  getProductListForCuisine(owner_id: string, cuisine: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/product/list';
    return this.httpClient.get(url, {
      params: { owner_id ,cuisine }
    }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
