import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class OfferService {

  constructor(private httpClient: HttpClient) { }
  
  addOffer(offer: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/offer';
    return this.httpClient.post(url, offer).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateOffer(offer: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/offer';
    return this.httpClient.put(url, offer).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  getOffer(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/offer';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
  
  deleteOffer(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/offer';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getOfferList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/offer/list';
    return this.httpClient.get(url, {
      params: {
        owner_id
      }
    }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }
}
