import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { SERVER_ENDPOINT } from 'src/environments/environment';
import { tap } from 'rxjs/operators';

@Injectable()
export class OrderService {

  constructor(private httpClient: HttpClient) { }

  createOrder(order: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/order';
    return this.httpClient.post(url, order).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  updateOrder(order: any): Observable<any> {
    const url = SERVER_ENDPOINT + '/order';
    return this.httpClient.put(url, order).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getOrder(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/order';
    return this.httpClient.get(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  deleteOrder(id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/order';
    return this.httpClient.delete(url + '/' + id).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }

  getOrderList(owner_id: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/order/list';
    return this.httpClient.get(url,
      {
        params: { owner_id }
      }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }


  getOrderListForCuisine(owner_id: string, cuisine: string): Observable<any> {
    const url = SERVER_ENDPOINT + '/order/list';
    return this.httpClient.get(url, {
      params: { owner_id ,cuisine }
    }).
      pipe(tap((data: any) => {
        console.log('data: ', data);
      }));
  }}
